<template>

    <div class="appBox">
        <div class="content">
            <ul>
                <li v-for="item in groupInfo.users"><router-link :to="{path:'usergroup', query:{uid:item.uid}}"><img :src="item.avatar" alt /><p>{{item.nickname}}</p></router-link></li>
            </ul>
            <!--  上面只返回20条用户信息，全部在更多成员  -->
            <div class="moreBtn"><a @click="showMore" href="javascript:void (0)">查看更多成员</a><i></i></div>
        </div>
        <ul class="myList">
            <li><a href="javascript:void(0);"><p>群聊名称</p><div class="groupName"><i>{{groupInfo.name}}</i></div></a></li>
            <li><a href="javascript:void(0);"><p>房间号</p><div class="groupName"><i>{{groupInfo.room_number}}</i></div></a></li>
            <li><router-link :to="{path:'/groupnotice', query:{groupId:this.redgroup_id}}"><p>群公告</p><span></span></router-link></li>
            <li><router-link :to="{path:'/grouprule', query:{groupId:this.redgroup_id}}"><p>抢红包规则</p><span></span></router-link ></li>
            <li><a href="javascript:void(0);"><p>置顶</p><div class="switch"><van-switch v-model="istop"   active-color="#07c160"  size=".22rem" /></div></a></li>
        </ul>
    </div>

</template>

<script>
    import { groupDetail, groupIstop } from '@/api/red'
    import Toast from 'vant/lib/toast';
    import 'vant/lib/toast/style';
    import { Switch } from 'vant';
    import 'vant/lib/switch/style';

    export default {
        name: "Reddetail",
        components: {
            [Switch.name]: Switch,
        },
        data()
        {
            return {

                redgroup_id:'',
                // 群信息
                groupInfo:{},
                istop:false

            }
        },
        watch:{

            istop(val)
            {
               this.groupIstop()
            }
        },
        mounted()
        {
            this.redgroup_id = this.$route.query.groupId
            this.getGroupInfo()
        },
        methods:{

            // 获取群信息
            getGroupInfo()
            {
                groupDetail({redgroup_id:this.redgroup_id}).then(res => {

                    this.groupInfo = res
                    this.istop = this.groupInfo.istop
                })
            },

            // 置顶
            groupIstop()
            {
                groupIstop({redgroup_id:this.redgroup_id}).then(res => {

                })
            },
            showMore()
            {
                Toast('暂无更多成员')
            }


        }
    }
</script>

<style scoped>
    @import "../../static/css/reddetail.css";
</style>



